<template>
	
	<div>
		<div v-for="item in datas" :key="item.name">
			<keyValMsgBlock :keys="item.keys" :val="item.val" :type="item.type" :widths="item.widths | ifEmpty" :name="item.name" @getSmsCode="getSmsCode" @changeFlag="changeFlag" :smsCodeFlag="ifChangeSmsCodeMsgFlag" :min="item.min" :max="item.max"></keyValMsgBlock>
		</div>
		<globalBtn class="submit_btn" :value="subminBtn.value" :status="subminBtn.status" @validateCode="validateCode" :btnState="btnState"></globalBtn>
	</div>
	
</template>

<script>
	
	import keyValMsgBlock from '@/components/keyValMsgBlock.vue'
	import loginInput from '@/components/loginInput.vue'
	import globalBtn from '@/components/globalBtn.vue'
	import { apiUrl } from '@/common/js/api'
	import { ajaxPost } from '@/common/js/public'
	import { Toast, Notify } from 'vant'
	
    export default {
        name: "register",
		components: {keyValMsgBlock, loginInput, globalBtn},
		data() {
			return {
				datas: [
					{
						name: 'name',
						keys: '真实姓名',
						val: '必须与身份证一致',
						type: 'text',
						min: 2,
						max: 30
					},
					{
						name: 'sex',
						keys: '性别',
						val: '',
						type: 'radio',
						min: 2,
						max: 30
					},
					{
						name: 'certificateNum',
						keys: '身份证号',
						val: '注册后无法修改',
						type: 'number',
						min: 15,
						max: 18
					},
					{
						name: 'password',
						keys: '输入密码',
						val: '请输入您的密码，长度为8-14位',
						type: 'password',
						min: 6,
						max: 30
					},
					{
						name: 'repassword',
						keys: '确认密码',
						val: '请确认您的密码',
						type: 'password',
						min: 6,
						max: 30
					},
					{
						name: 'phone',
						keys: '手机号码',
						val: '请输入手机号码',
						type: 'number',
						min: 2,
						max: 30
					},
					{
						name: 'smsCode',
						keys: '验证码',
						val: '请输入手机验证码',
						type: 'number',
						min: 6,
						max: 6
					},
					/* {
						keys: '医保卡类型',
						val: '',
					},
					{
						keys: '医保卡号',
						val: '请输入医保卡号'
					},
					{
						keys: '家庭住址',
						val: '请输入居住地址'
					}, */
				],
				subminBtn: {
					value: '提交注册',
					status: 'normal register_btn',
				},
				ifCanRegisterFlag: false,
				ifCanSmsCodeFlag: false,
				ifChangeSmsCodeMsgFlag: false,
				btnState: false
			}
		},
		filters: {
			ifEmpty(val) {
				if (val) {
					return val;
				} else {
					return '';
				}
			}
		},
		methods: {
			isCardNo(card) {
				// 身份证号码为15位或者18位，15位时全为数字，18位前17位为数字，最后一位是校验位，可能为数字或字符X 
				let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/; 
				if(reg.test(card) === false) { 
					return false; 
				}
				return true;
			},
			checkPhone(phone) {
				if(!(/^1[34578]\d{9}$/.test(phone))){ 
					return false; 
				}
				return true;
			},
			notifyIt(msg, time = 1000) {
				this.$notify({
					message: msg,
					duration: time,
				})
			},
			validateRegisterMsg() {
				let register = this.$store.state.pmodule.register;
				//身份证验证
				if (!this.isCardNo(register.certificateNum)) {
					this.notifyIt('身份证错误');
					console.log('身份证错误');
					return false;
				}
				
				//密码验证
				if (register.password.length > 14 || register.password.length < 8) {
					this.notifyIt('密码长度为8-14位');
					console.log('密码长度不合规范');
					return false;
				}
				
				if (register.password !== register.repassword) {
					this.notifyIt('两次密码不相同');
					console.log('两次密码不相同');
					return false;
				}
				
				//手机号验证
				if (!this.checkPhone(register.phone)) {
					this.notifyIt('手机号错误');
					console.log('手机号错误');
					return false;
				}
				
				return true;
			},
			validateCode() {
				let register = this.$store.state.pmodule.register;
				//非空验证
				for (let key in register) {
					if (register[key] == '') {
						this.ifCanRegisterFlag = false;
						console.log('完成填写');
						this.notifyIt('请完成填写');
						return;
					}
					this.ifCanRegisterFlag = true;
				}
				
				this.ifCanRegisterFlag = this.validateRegisterMsg();
				
				if (this.ifCanRegisterFlag) {
					this.btnState = true;
					console.log('注册!');
					var _this = this;
					/* ajaxPost(apiUrl.regiter, {
						"userName": register.name,
						"password": register.password,
						"phone": register.phone,
						"certificateNum": register.certificateNum,
						"smsCode": register.smsCode,
						"sex": register.sex,
					}, (data) => {
						data = data.data.body; */
						data = {
							resultCode: 1000
						}
						_this.btnState = false;
						/* 
							1001: 参数为空
							3001：验证码错误
							3002：验证码失效
							20002：手机已注册
							20001：身份证已注册
							1110：业务异常
							1000：成功	
						 */
						if (data.resultCode == 1000) {
							console.log('注册成功');
							//跳转登录
							_this.$router.push({name:'login'});
						} else if (data.resultCode == 3001) {
							console.log('验证码错误');
							_this.notifyIt('验证码错误');
						} else if (data.resultCode == 3002) {
							console.log('验证码失效,请重新获取');
							_this.notifyIt('验证码失效,请重新获取');
						} else if (data.resultCode == 30001) {
							console.log('短信验证码无效');
							_this.notifyIt('短信验证码无效');
						} else if (data.resultCode == 20001) {
							console.log('身份证已被注册');
							_this.notifyIt('身份证已被注册');
						} else if (data.resultCode == 20002) {
							console.log('手机号已被注册');
							_this.notifyIt('手机号已被注册');
						} else if (data.resultCode == 1110) {
							console.log('网络繁忙');
							_this.notifyIt('网络繁忙');
						}
					/* }, () => {
						_this.btnState = false;
						_this.notifyIt('网络繁忙');
					}) */
				}
			},
			//获取短信验证码
			getSmsCode() {
				let register = this.$store.state.pmodule.register;
				for (var key in register) {
					if (key != 'smsCode') {
						if (register[key] == '') {
							this.ifCanSmsCodeFlag = false;
							console.log('完成填写');
							this.notifyIt('请完成填写');
							return;
						}
					}
					this.ifCanSmsCodeFlag = true;
				}
				
				this.ifCanSmsCodeFlag = this.validateRegisterMsg();
				
				if (this.ifCanSmsCodeFlag) {
					var _this = this;
					ajaxPost(apiUrl.getSystemSMS, {
						"module": "register",
						"phone": register.phone
					}, (data) => {
						/* 
						1000 成功
						1001 参数为空
						1110 业务异常
						3004 发送太频繁
						20002 该手机已被注册
						3005 验证码次数已经达到上限
						*/
						data = data.data.body;
						if (data.resultCode == 1000) {
							console.log('发送验证码成功');
							_this.notifyIt('发送验证码成功');
							_this.ifChangeSmsCodeMsgFlag = true;
						} else if (data.resultCode == 1001) {
							console.log('参数为空');
							_this.notifyIt('参数为空');
						} else if (data.resultCode == 20002) {
							console.log('该手机已被注册');
							_this.notifyIt('该手机已被注册');
						} else if (data.resultCode == 3004) {
							console.log('操作过快');
							_this.notifyIt('操作过快');
						} else if (data.resultCode == 1110) {
							console.log('网络繁忙');
							_this.notifyIt('网络繁忙');
						}else if (data.resultCode == 3005) {
							console.log('今天您的验证码次数已经达到上限');
							_this.notifyIt('今天您的验证码次数已经达到上限');
						}
					}, () => {
						_this.notifyIt('网络繁忙');
					})
				}
			},
			changeFlag() {
				//改变可发送验证码的状态
				this.ifCanSmsCodeFlag = false;
				this.ifChangeSmsCodeMsgFlag = false;
			}
		}
    }

</script>

<style scoped lang="less">
	
	.submit_btn {
		margin-top: .5*2rem;
	}
	
</style>


