<template>
	<div>
		
		<div :keys="keys" :val="val" class="msg_block clear" :class="widths">
			<div class="msg_item msg_key">{{keys}}</div>
			<div class="msg_item msg_val">
				<div v-if="name == 'sex'" class="msg_item_radio">
					<div class="radio_item" @click="checkRadio($event)" data-val="0" :class="checkStatus == true ? 'checked' : ''">男</div>
					<div class="radio_item" @click="checkRadio($event)" data-val="1" :class="checkStatus == false ? 'checked' : ''">女</div>
				</div>
				<input :type="type" :placeholder="val" :name="name" v-else @input="changeValFn($event)" v-model="temporaryVal" :class="name == 'phone' ? 'length' : ''"  />
			</div>
			<div class="validate_code" v-show="name == 'phone'">
				<input @click="getSmsCodeForFather" type="button" :disabled="canCountDown == true ? 'disabled' : false" :value="countDownMsg" :class="canCountDown == true ? 'disabled_btn' : ''" />
			</div>
		</div>
		<!-- <div class="prompt_msg" v-show="name == 'certificateNum'">身份证号注册后无法修改</div> -->
		
	</div>
</template>

<script>
	
	import {mapMutations} from 'vuex'
	
	export default {
		name: 'keyValMsgBlock',
		data() {
			return {
				countDownMsg: '获取验证码',
				canCountDown: false,
				timeFlag: false,
				time: 60,
				temporaryVal: '',
				checkStatus: true
			};
		},
		props: ['keys', 'val', 'widths', 'name', 'smsCodeFlag', 'type'],
		methods: {
			...mapMutations(['pmodule/changeRegisterVal']),
			
			maxLength(val, name) {
				let max = 30;
				if (name == 'smsCode') {
					//验证码只显示六位
					max = 6;
				}
				if (val.length > max) {
					val = val.substring(0, max);
					this.temporaryVal = val;
					return val;
				}
				return val;
			},
			changeValFn(ele) {
				let val = ele.target.value;
				val = this.maxLength(val, ele.target.name);
				this.$store.commit('pmodule/changeRegisterVal', {name: ele.target.name, val: val});
				//console.log(ele.target.name)
				///console.log(this.$store.state.pmodule.register)
			},
			
			//获取短信验证码
			getSmsCodeForFather() {
				this.$emit('getSmsCode');
			},
			setPayTime() {
				if (!this.timeFlag) {
					this.timeFlag = true;
					//倒计时
					let timer;
					let setTime = () => {
						if(this.time == 0) {
							this.timeFlag = false;
							this.time = 60;
						} else {
							this.time--;
							timer = setTimeout(function() {
								setTime();
							}, 1000)
						}
					}
					setTime();
				}
			},
			
			checkRadio($event) {
				this.checkStatus = !this.checkStatus;
				//console.log($event.target.dataset.val);
				this.$store.commit('pmodule/changeRegisterVal', {name: 'sex', val: $event.target.dataset.val});
			}
		},
		watch: {
			smsCodeFlag(newVal, oldVal) {
				this.canCountDown = newVal;
			},
			canCountDown(newVal, oldVal) {
				if (newVal) {
					//开始倒计时
					this.setPayTime();
					return;
				}
			},
			time(newVal, oldVal) {
				//console.log(oldVal > 0);
				if (oldVal > 0) {
					this.countDownMsg =  '重新获取('+ this.time +'s)';
				} else {
					this.canCountDown = false;
					this.$emit('changeFlag')
					this.countDownMsg = '获取验证码';
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	@import '../common/css/global_pyt.less';
	
	.msg_block {
		background-color: #fff;
		padding: .12*2rem .16*2rem;
		font-size: .14*2rem;
		box-sizing: border-box;
		border-bottom: 1px solid #F6F7F8;
		color: #272727;
		position: relative;
		line-height: .2*2rem;
		.msg_item {
			display: inline-block;
			height: .2*2rem;
			width: calc(100% - .56*2rem - (.16*2rem * 2));
			input {
				border: none;
				padding-left: .2*2rem;
				width: 100%;
			}
			input.length {
				width: calc(100% - .8*2rem);
			}
			.msg_item_radio {
				padding-left: .2*2rem;
				.radio_item {
					margin-right: .05*2rem;
					display: inline-block;
					width: .5*2rem;
					&:before {
						content: "";
						display: inline-block;
						position: relative;
						top: .02*2rem;
						width: .14*2rem;
						height: .14*2rem;
						margin-right: .06*2rem;
						background-image: url(../assets/register_cancel.png);
						background-size: 100%;
					}
				}
				.radio_item.checked {
					&:before {
						background-image: url(../assets/register_select.png);
					}
				}
			}
		}
		.msg_key {
			width: .56*2rem;
			text-align: justify;
			text-align-last: justify;
		}
		.validate_code {
			position: absolute;
			width: .84*2rem;
			height: .3*2rem;
			top: .08*2rem;
			right: .16*2rem;
			input {
				width: 100%;
				height: 100%;
				line-height: .3*2rem;
				border: 1px solid #3391E8;
				border-radius: 2px;
				box-sizing: border-box;
				text-align: center;
				font-size: .12*2rem;
				color: #3391E8;
				background-color: #fff;
				padding: 0;
			}
			.disabled_btn {
				color: #B1AFAF;
				border-color: #B1AFAF;
				
			}
		}
	}
	
	.long {
		.msg_key {
			width: .7*2rem;
		}
	}
	
	.prompt_msg {
		color: #DA1616;
		font-size: .1*2rem;
		text-align: right;
		padding: .04*2rem .16*2rem .22*2rem 0;
		border-bottom: 1px solid #F6F7F8;
	}
	
</style>



