<template>
	<div>
		<div class="login_input_block">
			<div class="login_input_icon">
				<span>
					<img :src="icon" class="icon_img" v-show="howLong != 'vertical'" />
				</span>
			</div>
			<!-- v-model="temporaryVal"  -->
			<input :type="type" :value="value" :placeholder="placeholder" class="login_input_item" :class="howLong" @input="changeValFn($event.target.value)" />
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				temporaryVal: ''
			};
		},
		props: ['type', 'placeholder', 'value', 'howLong', 'icon'],
		methods: {
			maxLength(val) {
				let max = 30;
				if (val.length > max) {
					val = val.substring(0, max);
					this.temporaryVal = val;
					return val;
				}
				return val;
			},
			changeValFn(newVal) {
				//newVal = this.maxLength(newVal);
				this.$emit('changeCode', newVal);
			},
		},
	}
</script>

<style lang="less" scoped>
	@font_size: .14*2rem;
	@border_radius: .04*2rem;
	@border: 1px solid #ccc;
	@width: 2.9*2rem;
	
	.box {
		display: block;
		margin: 0 auto;
	}
	
	.box_sizing {
		box-sizing: border-box;
	}
	
	/* ============================= */
	
	.login_input_block {
		&::input-placeholder {
			color: #B1AFAF;
		}
		&::placeholder {
			color: #B1AFAF;
		}
		.login_input_icon {
			width: @width;
			transform: translateX(-50%);
			left: 50%;
			position: absolute;
			.icon_img {
				position: absolute;
				left: .06*2rem;
				top: .12*2rem;
				width: .16*2rem;
			}
		}
		
		.login_input_item {
			.box_sizing;
			.box;
			height: 0.44*2rem;
			font-size: @font_size;
			margin-bottom: .14*2rem;
			padding-left: .28*2rem;
			border-radius: @border_radius;
			border: @border;
			color: #272727;
		}
		
		.login_input_item.normal {
			width: @width;
		}
		
		.login_input_item.vertical {
			width: 1.48*2rem;
			margin-left: 0;
			padding-left: .08*2rem;
			margin-bottom: .32*2rem;
		}
	}
	
	
	
</style>



